import React, { useEffect, useState } from "react";
import Calendar from "react-calendar";
import moment from "moment-timezone";
import { FaClock } from "react-icons/fa";
import { useParams } from "react-router-dom";
import Autocomplete from "@mui/material/Autocomplete";
import { Paper, Typography, Divider, TextField, Grid, Select, MenuItem, Box } from "@mui/material";
import TimeSlotsContainer from "../../Components/TimeSlotsContainer/TimeSlotsContainer";
import useStylesCalendar from "./stylesCalendar";
import auth from "../../auth";
import "./CalendarComponent.css";

import { getUserByUrlFunc } from "../../actions/user";
import { getDaysAvailableFunc, getTimeSlotFunc } from "../../actions/availability";
import { getIntentList } from "../../actions/user";
import { apiGetAllDentists } from "../../actions/dentist";

function getDayOfWeek(index) {
  const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
  return days[index];
}

function getMonth(index) {
  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  return months[index];
}
const CalendarPage = () => {
  console.log("================");
  const classes = useStylesCalendar();
  const [clientTz, setClientTz] = useState("Europe/Berlin");
  const today = moment.tz(clientTz);
  const [calendarVal, setCalendarVal] = useState(null);
  const [strDate, setDate] = useState(null); //timeslot header display
  const [userName, setUserName] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [meeting, setMeeting] = useState("");
  const [showTimeSlots, setShowTimeSlots] = useState(false);
  const [timeSlots, setTimeSlots] = useState([]);
  const [availableDays, setAvailableDays] = useState([]);
  const [currMonth, setCurrMonth] = useState(today.month());
  const [meetingName, setMeetingName] = useState("");
  const [dentists, setDentists] = useState([]);
  const [dentist, setDentist] = useState("");
  const [eventTypes, setEventTypes] = useState([]);

  let { sub, eventDuration, event, url } = useParams();

  useEffect(() => {
    //fetch user data
    getUserByUrlFunc(url, eventDuration)
      .then((res) => {
        if (res.status === 200) {
          const data = res.data;
          data.fullName ? setUserName(data.fullName) : setUserName(data.given_name);
          window.userData = data;
          //find specific meeting type
          const meeting = data.meetings.filter((meeting) => meeting._id === event);
          setMeeting(meeting[0]);
        }
      })
      .catch((e) => console.log("Error " + e));
  }, [url, eventDuration]);

  React.useEffect(() => {
    getIntentList().then((res) => {
      setEventTypes(res.data);
    });
    apiGetAllDentists(sub)
      .then((res) => {
        res.status === 200 && setDentists(res.data.docs);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  useEffect(() => {
    // function fetchAvailableDays
    getDaysAvailableFunc(currMonth, eventDuration, clientTz, url).then((res) => {
      if (res.status === 200) {
        setAvailableDays(res.data.days);
        setShowTimeSlots(false);
        setCalendarVal(null);
      }
    });
  }, [clientTz, currMonth, eventDuration, url]);

  const setMaxDate = () => {
    const date = new Date();
    const maxDate = new Date(date.setMonth(date.getMonth() + 3, 31));
    return maxDate;
  };

  const setMinDate = () => {
    const minDate = new Date(moment().tz(clientTz).format());
    return minDate;
  };

  const fetchAvailableTimeSlots = (date, month) => {
    getTimeSlotFunc(month, date, eventDuration, clientTz, url)
      .then((res) => {
        if (res.status === 200) {
          setTimeSlots(res.data.slots);
          setLoading(false);
        }
      })
      .catch((err) => console.log(err));
  };

  const handleClick = (value) => {
    const date = value.getDate();
    const month = value.getMonth();
    const strDay = getDayOfWeek(value.getDay());
    const strMonth = getMonth(value.getMonth());
    setCalendarVal(value);
    setDate(`${strDay}, ${strMonth} ${date}`);
    setShowTimeSlots(true);
    setLoading(true);
    fetchAvailableTimeSlots(date, month);
  };

  const handleMonth = (event) => {
    setCurrMonth(event.activeStartDate.getMonth());
  };

  const tileDisabled = (activeStartDate) => {
    const day = activeStartDate.date.getDate();
    if (availableDays.indexOf(day) < 0) return true;
  };

  const tileContent = (date) => {
    if (date.date.getMonth() === today.month() && date.date.getDate() === today.date()) {
      return <div id="today">*</div>;
    } else return null;
  };

  return (
    // <Paper elevation={2} className={classes.calendarContainer}>
    <Box width="50%" style={{ margin: "1rem auto", borderRadius: "10px", boxShadow: "5px 10px 18px #888888" }}>
      <Grid container className="calendar">
        <Grid item md={12} sm={12} sx={12}>
          <Box p={2}>
            <Typography className={classes.meetingName} variant="h4">
              {userName}
            </Typography>
            <Typography className={classes.meetingName} variant="h6">
              {meeting.meetingName}
            </Typography>
            <Divider />
            <Box display="flex" width="100%">
              <Box mt={1} width="50%" p={1}>
                Absicht
                <Select
                  fullWidth
                  value={meetingName}
                  onChange={(e) => {
                    setMeetingName(e.target.value);
                  }}
                >
                  {eventTypes?.map((item) => (
                    <MenuItem key={item.displayName} value={item.displayName}>
                      {item.displayName}
                    </MenuItem>
                  ))}
                </Select>
              </Box>
              <Box mt={1} width="50%" p={1}>
                Behandler
                <Select fullWidth value={dentist} onChange={(e) => setDentist(e.target.value)}>
                  {dentists.map((item) => (
                    <MenuItem key={`${item?._id}`} value={item?._id}>
                      Dr.{item?.name}
                    </MenuItem>
                  ))}
                </Select>
              </Box>
            </Box>
            {/* <Box mt={2}>
              <Autocomplete
                id="timezone-field"
                disableClearable={true}
                className={classes.timezone}
                value={clientTz}
                options={["Europe/Berlin"]}
                getOptionLabel={(option) => option.replace("_", " ") + " (GMT" + moment.tz(option).format("Z") + ")"}
                renderInput={(params) => <TextField {...params} label="Type a city or zone" variant="outlined" />}
                onChange={(e, v) => setClientTz(v)}
              />
            </Box> */}
          </Box>
        </Grid>
        <Grid item md={7} sm={12} sx={12} mt={1}>
          <div className={classes.selectDay}>
            <div className={classes.calendar}>
              <Calendar
                onActiveStartDateChange={handleMonth}
                tileContent={tileContent}
                minDate={setMinDate()}
                maxDate={setMaxDate()}
                onClickDay={handleClick}
                value={calendarVal}
                tileDisabled={tileDisabled}
              />
            </div>
          </div>
        </Grid>
        <Grid item md={5} sm={12} sx={12}>
          <div style={{ padding: "0px 32px 16px 32px" }}>
            {showTimeSlots ? (
              <TimeSlotsContainer
                userName={userName}
                sub={sub}
                meeting={meeting}
                availableTimes={timeSlots}
                // date={strDate}
                isLoading={isLoading}
                clientTz={clientTz}
                meetingName={meetingName}
                dentist={dentist}
              />
            ) : null}
          </div>
        </Grid>
      </Grid>
    </Box>
    // </Paper>
  );
};

export default CalendarPage;
