import React from "react";
import useStylesTimeSlots from "./stylesTimeSlots";
import TimeSlot from "../TimeSlot/TimeSlot";
import PropTypes from "prop-types";
import { CircularProgress } from "@mui/material";

function TimeSlotsContainer({
  isLoading,
  meetingName,
  dentist,
  sub,
  availableTimes,
  date,
  userName,
  meeting,
  clientTz,
}) {
  const classes = useStylesTimeSlots();
  return (
    <div className={classes.timeSlotContainer}>
      <p>{date}</p>
      <div className={classes.btns}>
        {isLoading ? (
          <div className={classes.loader}>
            <CircularProgress />
          </div>
        ) : (
          availableTimes.map((time, index) => (
            <TimeSlot
              sub={sub}
              userName={userName}
              meeting={meeting}
              meetingName={meetingName}
              dentist={dentist}
              key={index}
              time={time}
              clientTz={clientTz}
            />
          ))
        )}
      </div>
    </div>
  );
}

TimeSlotsContainer.propTypes = {
  date: PropTypes.string.isRequired,
  userName: PropTypes.string.isRequired,
  meeting: PropTypes.object.isRequired,
  availableTimes: PropTypes.array.isRequired,
  clientTz: PropTypes.string.isRequired,
};

export default TimeSlotsContainer;
