import { makeStyles } from "@mui/styles";

const useStylesCalendar = makeStyles({
  calendarContainer: {
    display: "flex",
    justifyContent: "flex-start",
    margin: "3rem auto",
    width: "50%",
    height: "600px",
  },
  calendar: {
    // flex: '1 0 auto',
    fontSize: "1.0rem",
    paddingRight: "2rem",

    "& h5": {
      marginBottom: "1.5rem",
      color: "#484848",
      fontWeight: "bold",
    },
  },
  eventInfo: {
    width: "385px",
    flex: "1 0 auto",
    padding: "1.5rem",
    "& h4": {
      color: "#484848",
      letterSpacing: "1px",
      fontWeight: "bold",
    },
    "& h6": {
      color: "gray",
      marginBottom: "0.5rem",
    },
  },
  duration: {
    color: "grey",
    marginTop: "0.5rem",
    display: "flex",
    "& p": {
      marginLeft: "0.5rem",
    },
  },
  selectDay: {
    display: "flex",
    padding: "0 32px 16px 32px",
  },
  meetingName: {
    textTransform: "capitalize",
  },
  timezone: {
    width: "100%",
    padding: "1rem 0.4rem",
  },
  "@media (max-width: 1600px)": {
    eventInfo: {
      textAlign: "center",
      width: "100%",
    },
    calendarContainer: {
      flexDirection: "column",
      height: "80%",
      width: "50%",
    },
    duration: {
      justifyContent: "center",
    },
    calendar: {
      paddingRight: "0",
    },
  },
  "@media (max-width: 1400px)": {
    calendarContainer: {
      width: "50%",
    },
  },
  "@media (max-width: 1260px)": {
    calendarContainer: {
      width: "50%",
    },
  },
  "@media (max-width: 1000px)": {
    calendarContainer: {
      width: "60%",
    },
    selectDay: {
      display: "block",
    },
    calendar: {
      textAlign: "center",
    },
  },
});

export default useStylesCalendar;
