import React, { useState } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";
import { useHistory, Link } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { FaRegEnvelope } from "react-icons/fa";
import { Paper, Box, TextField, InputAdornment, Divider } from "@mui/material";

import stylesForm from "./stylesForm";
import LogoImg from "../../Pages/Assets/brandmark-design.png";
import { apiGetEmailVerificationCode } from "../../actions/user";

const ForgotForm = ({ classes }) => {
  const history = useHistory();
  const [email, setEmail] = useState();
  const sendEmailFunc = () => {
    if (!email) toast.error("Fgen Sie die E-Mail ein.");
    else {
      apiGetEmailVerificationCode({ email: email })
        .then((res) => {
          localStorage.setItem("dento", res.data.doc.token);
          toast.success("Geben Sie den Besttigungscode aus Ihrer E-Mail ein!");
          res.status === 200 && history.push("/verify");
        })
        .catch((err) => console.log(err));
    }
  };
  const handleKeyDown = (event) => {
    if (event.key === "Enter") {
      // Handle the Enter key press, e.g. submit the form
      console.log("Enter key pressed");
    }
  };

  return (
    <Paper elevation={6} className={classes.paper} style={{ marginTop: "45px" }}>
      <Box display="flex" justifyContent="center" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "50%" }} />
      </Box>

      <TextField
        name="email"
        label="E-mail"
        type="text"
        variant="outlined"
        margin="normal"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <FaRegEnvelope />
            </InputAdornment>
          ),
        }}
      />

      <div id="authButton" className="buttonContainer" onClick={sendEmailFunc} onKeyDown={handleKeyDown}>
        <span>E-Mail Senden</span>
      </div>
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          Sie haben kein Konto?
          <Link to="/login">Sign In</Link>
        </Box>
      </Box>
    </Paper>
  );
};

ForgotForm.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(stylesForm)(ForgotForm);
