import axios from "axios";
import { dentist_URL } from "./constant";

export const apiAddDentist = (dentist, subId) => {
  console.log(subId, "===========");

  return axios
    .post(`${dentist_URL}/${subId}`, dentist)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const apiGetAllDentists = (subId) => {
  return axios
    .get(`${dentist_URL}/${subId}`)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const apiUpdateDentist = (dentist, _id) => {
  return axios
    .put(`${dentist_URL}/${_id}`, dentist)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
export const apiDeleteDentist = (id) => {
  return axios
    .delete(`${dentist_URL}/${id}`)
    .then((res) => {
      return res;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
