import React, { useState } from "react";
import PropTypes from "prop-types";
import { toast } from "react-toastify";
import { useHistory, Link } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { FaLock } from "react-icons/fa";
import { Paper, Box, TextField, InputAdornment, Divider } from "@mui/material";
import { Visibility as IconVisiblility, VisibilityOff as IconVisibilityOff } from "@mui/icons-material";

import stylesForm from "./stylesForm";
import LogoImg from "../../Pages/Assets/brandmark-design.png";
import { apiResetPassword } from "../../actions/user";

const ResetPasswordForm = ({ classes }) => {
  const history = useHistory();
  const [visibility, setVisibility] = React.useState(false);

  const [password, setPassword] = useState("");
  const [confirm, setConfirm] = useState("");
  const resetPasswordFuc = () => {
    if (password !== confirm) toast.error("Das Passwort stimmt nicht mit dem Besttigungspasswort berein.");
    else if (password === "") {
      toast.error("Bitte geben Sie Ihr Passwort ein.");
    } else {
      apiResetPassword(password)
        .then((res) => {
          if (res.status === 200) {
            toast.success("Wir senden das genderte Passwort an Ihre E-Mail.");
            history.push("/login");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };

  return (
    <Paper elevation={6} className={classes.paper} style={{ marginTop: "45px" }}>
      <Box display="flex" justifyContent="center" className={classes.loginHeader}>
        <img src={LogoImg} alt="logo_img" style={{ width: "50%" }} />
      </Box>

      <TextField
        name="password"
        label="Passwort"
        type={visibility === true ? "text" : "password"}
        variant="outlined"
        value={password}
        size="small"
        autoFocus
        margin="normal"
        onChange={(e) => setPassword(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <FaLock />
            </InputAdornment>
          ),
          endAdornment: (
            <InputAdornment className={classes.adornment} position="start" component="div">
              {visibility === true ? (
                <div
                  style={{ cursor: "pointer" }}
                  onClick={(e) => {
                    setVisibility(false);
                  }}
                >
                  <IconVisibilityOff />
                </div>
              ) : (
                <div style={{ cursor: "pointer" }} onClick={(e) => setVisibility(true)}>
                  <IconVisiblility />
                </div>
              )}
            </InputAdornment>
          ),
        }}
      />
      <TextField
        name="confirm"
        label="Besttigen Passwort"
        type={visibility === true ? "text" : "password"}
        variant="outlined"
        value={confirm}
        autoFocus
        size="small"
        margin="normal"
        onChange={(e) => setConfirm(e.target.value)}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <FaLock />
            </InputAdornment>
          ),
          endAdornment: (
            <InputAdornment className={classes.adornment} position="start" component="div">
              {visibility === true ? (
                <div
                  style={{ cursor: "pointer" }}
                  onClick={(e) => {
                    setVisibility(false);
                  }}
                >
                  <IconVisibilityOff />
                </div>
              ) : (
                <div style={{ cursor: "pointer" }} onClick={(e) => setVisibility(true)}>
                  <IconVisiblility />
                </div>
              )}
            </InputAdornment>
          ),
        }}
      />

      <div id="authButton" className="buttonContainer" onClick={resetPasswordFuc}>
        <span>Passwort zurcksetzen</span>
      </div>
      <Box style={{ marginTop: 10 }}>
        <Divider />
        <Box className={classes.helpText}>
          Sie haben kein Konto?
          <Link to="/login">Sign In</Link>
        </Box>
      </Box>
    </Paper>
  );
};

ResetPasswordForm.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(stylesForm)(ResetPasswordForm);
