import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { NavLink, useHistory, useLocation, useParams } from "react-router-dom";

import { withStyles } from "@mui/styles";
import { Paper, Button, Box, Switch, Checkbox, FormControlLabel, FormGroup, Grid } from "@mui/material";
import { ValidatorForm, TextValidator } from "react-material-ui-form-validator";
import stylesConfirm from "./stylesConfirm";
import { submitAppointmentFunc } from "../../actions/appointment";
import * as moment from "moment-timezone";
import { apiGetAllDentists } from "../../actions/dentist";

const ConfirmSchedule = ({ classes }) => {
  const weekdays = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  const location = useLocation();
  console.log(location.state.dentist);
  const { url } = useParams();
  const clientTz = location.state.clientTz;
  const appt = moment.tz(location.state.time, clientTz);
  const apptTime = appt.format(); //do not move down, moment is mutable

  const appointmentStr = `${appt.format("h:mma")} - ${appt.add(location.state.meeting.duration, "m").format("h:mma")}
  ${weekdays[appt.day()]}, ${months[appt.month()]} ${appt.date()}, ${appt.year()}`;
  const user = window.userData;
  const [firstName, setFirstName] = useState("");
  const [patientType, setPatientType] = useState(false);
  const [lastName, setLastName] = useState("");
  const [emailField, setEmail] = useState("");
  const [comment, setComment] = useState("");
  const [birthday, setBirthday] = useState("");
  const [agreeRule, setAgreeRule] = useState(null);
  const [dentists, setDentists] = useState([]);
  const [dentistName, setDentistName] = useState("");
  useEffect(() => {
    if (url === "demo") {
      alert("This is a demo account. Scheduling an event will not add it to any google accounts.");
    }
    apiGetAllDentists(location.state.sub).then((res) => {
      res.status === 200 && setDentists(res.data.docs);
      const dentist = res.data.docs.filter((d) => d._id === location.state.dentist);
      setDentistName(dentist[0].name);
    });
  }, [url]);

  const handleName = (e) => {
    setFirstName(e.target.value);
  };

  const handleEmail = (e) => {
    setEmail(e.target.value);
  };

  let history = useHistory();

  const submitForm = () => {
    const appointmentInfo = {
      guestFirstName: firstName,
      guestLastName: lastName,
      guestEmail: emailField,
      guestComment: comment,
      guestTz: clientTz,
      guestBirthday: birthday,
      guestPatientType: patientType,
      guestAgreeRule: agreeRule,
      meetingName: location.state.meetingName,
      meetTime: location.state.meeting.duration,
      apptTime: apptTime,
      url,
      dentistId: location.state.dentist,
    };

    submitAppointmentFunc(appointmentInfo).then((res) => {
      if (res.status !== 201) {
        history.push(`/${url}/${location.state.meeting.duration}`); //go back to scheduler, error alert?
      } else {
        history.push("/finish", {
          url: url,
          name: location.state.name,
          meetingName: location.state.meeting.meetingName,
          dentist: location.state?.dentist,
          apptTime: appointmentStr,
          timezone: location.state.clientTz,
        });
      }
    });
  };

  return (
    <Paper className={classes.paper}>
      <ValidatorForm onSubmit={submitForm} className="confirm-schedule">
        <Box display="flex" alignItems="center" justifyCotent="center" flexDirection="column">
          <Box>
            <h1 style={{ color: "#2e2e2e" }}>persnliche Daten</h1>
          </Box>
          {/* <Box
              display="flex"
              alignItems={"center"}
              padding={1}
              style={{ border: "solid 1px #e2e2e2", color: "#00BFB1", wordBreak: "normal" }}
            > */}
          <Grid
            container
            style={{
              border: "solid 1px #e2e2e2",
              color: "#00BFB1",
              wordBreak: "normal",
              paddingRight: "12px",
              paddingLeft: "12px",
              marginBottom: "16px",
            }}
          >
            <Grid item md={4} sm={12} xs={12}>
              <p style={{ paddingRight: "4px", paddingLeft: "4px", textAlign: "center" }}>
                Termind: {appt.format("DD-MM-YYYY")}
              </p>
            </Grid>
            <Grid item md={4} sm={12} xs={12}>
              <p style={{ paddingRight: "4px", paddingLeft: "4px", textAlign: "center" }}>
                {location.state.meetingName}
              </p>
            </Grid>
            <Grid item md={4} sm={12} xs={12}>
              <p style={{ paddingRight: "4px", paddingLeft: "4px", textAlign: "center" }}>Dr.{dentistName}</p>
            </Grid>
          </Grid>
          <Grid container spacing={4}>
            {/* </Box> */}
            <Grid item md={6} sm={12} xs={12}>
              <Box pt={2}>Vorname*</Box>
              <TextValidator
                className={classes.textField}
                id="name-field"
                variant="outlined"
                name="nameField"
                value={firstName}
                validators={["required"]}
                errorMessages={["FirstName field is required"]}
                onChange={handleName}
              />
            </Grid>
            <Grid item md={6} sm={12} xs={12}>
              <Box pt={2}>Nachname*</Box>
              <TextValidator
                name="nameField"
                value={lastName}
                validators={["required"]}
                className={classes.textField}
                errorMessages={["LastName field is required"]}
                onChange={(e) => setLastName(e.target.value)}
              />
            </Grid>
            <Grid item md={6} sm={12} xs={12}>
              <Box pr={1}>Email</Box>
              <TextValidator
                className={classes.textField}
                variant="outlined"
                onChange={handleEmail}
                name="email"
                value={emailField}
                validators={["required", "isEmail"]}
                errorMessages={["Email field is required", "email is not valid"]}
              />
            </Grid>
            <Grid item md={6} sm={12} xs={12}>
              <Box pr={1}>Telefon*</Box>
              <TextValidator className={classes.textField} />
            </Grid>
            <Grid item md={6} sm={12} xs={12}>
              <Box pr={1}>Geburtsdatum*</Box>

              <TextValidator
                name="birthday"
                value={birthday}
                validators={["required"]}
                className={classes.textField}
                errorMessages={["Birthday field is required"]}
                onChange={(e) => setBirthday(e.target.value)}
              />
            </Grid>
            <Grid item md={12} sm={12} xs={12} style={{ display: "flex" }}>
              <Box display="flex" alignItems={"flex-end"}>
                <Box display={"flex"} alignItems="center">
                  <Box>Bestandspatient</Box>
                  <Box>
                    <Switch checked={patientType} onChange={(e) => setPatientType(!patientType)} />
                  </Box>
                  <Box>NeuerPatient</Box>
                </Box>
              </Box>
            </Grid>
            <Grid item md={12} sm={12} xs={12}>
              <Box p={1}>Nachricht</Box>

              <TextValidator multiline rows={2} fullWidth onChange={(e) => setComment(e.target.value)} />
            </Grid>
            <Grid item md={12} sm={12} xs={12}>
              <FormGroup>
                <FormControlLabel
                  control={<Checkbox checked={agreeRule === false} onChange={(e) => setAgreeRule(false)} />}
                  label="Ich bin damit elnverstanden, zweks Teminbesta"
                />
                <FormControlLabel
                  control={<Checkbox checked={agreeRule === true} onChange={(e) => setAgreeRule(true)} />}
                  label={
                    <>
                      Ich aktzeptiere die <NavLink to="">Dateenschtzbestimmungen</NavLink>
                    </>
                  }
                />
              </FormGroup>
            </Grid>
            <Grid item md={12} sm={12} xs={12}>
              <Box display="flex" mt={4} justifyContent="flex-end">
                <Button variant="outlined" style={{ marginRight: "8px" }}>
                  Abbrechen
                </Button>
                <Button variant="contained" type="submit">
                  Buchung bestatigen
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </ValidatorForm>
    </Paper>
  );
};
ConfirmSchedule.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(stylesConfirm)(ConfirmSchedule);
