import React, { useState } from "react";
import {
  Box,
  Button,
  Grid,
  IconButton,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
  Tooltip,
  InputAdornment,
} from "@mui/material";
import {
  Delete as IconDelete,
  Edit as IconEdit,
  AccountCircleOutlined as IconAccount,
  EmailOutlined as IconEmail,
  PhoneAndroidOutlined as IconPhone,
  Add as IconAdd,
  Save as IconSave,
  Clear as IconClear,
} from "@mui/icons-material";
import { toast } from "react-toastify";
import ConfirmDialog from "../../Components/Confirm/ConfirmDialog";

import auth from "../../auth";
import useStyleDentist from "./styelDentist";

import { getUserByIdFunc } from "../../actions/user";
import { apiAddDentist, apiDeleteDentist, apiGetAllDentists, apiUpdateDentist } from "../../actions/dentist";

const PageDentist = () => {
  const classes = useStyleDentist();
  const [openDialog, setOpenDialog] = useState(false);
  const [user, setUser] = useState({ meetings: [] });
  const [subId, setSubId] = useState("");
  const initialDentist = { userId: user._id, name: "", email: "", phoneNumber: "" };
  const [dentist, setDentist] = useState(initialDentist);
  const [dentists, setDentists] = useState([]);
  const [deleteId, setDeleteId] = useState("");

  React.useEffect(() => {
    const subId = auth.getSub();
    setSubId(subId);
    console.log(subId);
    getUserByIdFunc(subId)
      .then((res) => {
        setUser(res.data);
        setDentist({ ...dentist, userId: res.data._id });
      })
      .catch((e) => {
        console.log("Error: " + e);
      });
    subId &&
      apiGetAllDentists(subId)
        .then((res) => {
          console.log(res.data);
          setDentists(res.data.docs);
        })
        .catch((err) => {
          console.log("Error: " + err);
        });
  }, []);

  const onChange = (e) => {
    setDentist({ ...dentist, [e.target.name]: e.target.value });
  };
  const handleSubmit = () => {
    apiAddDentist(dentist, subId)
      .then((res) => {
        toast.success("Success Add New Dentist!");
        res.status === 200 && setDentists([...dentists, res.data.doc]);
        setDentist(initialDentist);
      })
      .catch((err) => {});
  };
  const handleSave = () => {
    apiUpdateDentist(dentist, dentist._id).then((res) => {
      if (res.status === 200) {
        toast.success("Dentist was successfully updated.");
        setDeleteId("");
        setDentists(dentists.map((item) => (item._id === dentist._id ? res.data.doc : item)));

        setOpenDialog(false);
      }
    });
  };
  const handleDelete = () => {
    apiDeleteDentist(deleteId)
      .then((res) => {
        if (res.status === 200) {
          toast.success("Dentist was successfully deleted.");
          setDentists(dentists.filter((dentist) => dentist._id !== deleteId));
          setDeleteId("");
          setDentist(initialDentist);
          setOpenDialog(false);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <div className={classes.container}>
      <Paper elevation={6} className={classes.paper} style={{ marginTop: "32px", paddingTop: "30px" }}>
        <Grid container spacing={2}>
          <Grid item md={4} sm={12} xs={12}>
            <Box display={"flex"} justifyContent={"center"} flexDirection={"column"} height="100%">
              <Box height="100%" style={{ border: "1px solid #e2e2e2", padding: 25, borderRadius: "5px" }}>
                <Box pb={1}>Dentist Name:</Box>
                <Box pb={2}>
                  <TextField
                    pl={2}
                    pr={2}
                    variant="outlined"
                    fullWidth
                    type="text"
                    required
                    name="name"
                    value={dentist.name}
                    onChange={onChange}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <IconAccount />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Box>
                <Box pb={2}>
                  <Box pb={1}>Dentist Email:</Box>
                  <TextField
                    pl={2}
                    pr={2}
                    variant="outlined"
                    fullWidth
                    type="email"
                    required
                    name="email"
                    value={dentist.email}
                    onChange={onChange}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <IconEmail />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Box>
                <Box>
                  <Box pb={1}>Dentist PhoneNumber:</Box>
                  <TextField
                    pl={2}
                    pr={2}
                    variant="outlined"
                    fullWidth
                    type="number"
                    name="phoneNumber"
                    value={dentist.phoneNumber}
                    onChange={onChange}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <IconPhone />
                        </InputAdornment>
                      ),
                    }}
                  />
                </Box>

                <Box mt={2} display={"flex"} justifyContent={"flex-end"}>
                  {dentist._id ? (
                    <Box display="flex">
                      <Tooltip title="save">
                        <Button
                          variant="contained"
                          color="primary"
                          startIcon={<IconSave />}
                          onClick={(e) => setOpenDialog(true)}
                          style={{ marginRight: "8px" }}
                        >
                          Save
                        </Button>
                      </Tooltip>
                      <Tooltip title="cancel">
                        <Button
                          variant="contained"
                          pl={2}
                          color="secondary"
                          startIcon={<IconClear />}
                          onClick={(e) => setDentist(initialDentist)}
                        >
                          Cancel
                        </Button>
                      </Tooltip>
                    </Box>
                  ) : (
                    <Tooltip title="add">
                      <Button variant="outlined" color="primary" startIcon={<IconAdd />} onClick={handleSubmit}>
                        Add
                      </Button>
                    </Tooltip>
                  )}
                </Box>
              </Box>
            </Box>
          </Grid>
          <Grid item md={8} sm={12} xs={12}>
            <Box style={{ border: "1px solid #e2e2e2", padding: 25, borderRadius: "5px" }}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell align="center" width={"10%"}>
                      No
                    </TableCell>
                    <TableCell align="center" width={"20%"}>
                      Name
                    </TableCell>
                    <TableCell align="center" width={"25%"}>
                      Email
                    </TableCell>
                    <TableCell align="center" width={"20%"}>
                      PhoneNumber
                    </TableCell>
                    <TableCell align="center" width={"25%"}></TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {dentists.map((dentist, index) => (
                    <TableRow key={index}>
                      <TableCell align="center">{index + 1}</TableCell>
                      <TableCell align="center">{dentist.name}</TableCell>
                      <TableCell align="center">{dentist.email}</TableCell>
                      <TableCell align="center">{dentist.phoneNumber}</TableCell>
                      <TableCell align="center" p={{ md: 16, sm: 8, xs: 2 }}>
                        <Tooltip title="edit" aria-label="edit">
                          <IconButton
                            aria-label="edit"
                            color="primary"
                            onClick={(e) => {
                              setDeleteId("");
                              setDentist(dentist);
                            }}
                          >
                            <IconEdit />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="delete" aria-label="delete">
                          <IconButton
                            aria-label="delete"
                            onClick={(e) => {
                              setOpenDialog(true);
                              setDeleteId(dentist._id);
                            }}
                          >
                            <IconDelete />
                          </IconButton>
                        </Tooltip>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Box>
          </Grid>
        </Grid>
        <ConfirmDialog
          title={"Confirm Dialog"}
          content={`Would you sure ${!deleteId ? "save" : "delete"} this Dentist?`}
          openDialog={openDialog}
          onOK={(e) => {
            !deleteId ? handleSave() : handleDelete();
          }}
          onCancel={(e) => {
            setOpenDialog(false);
          }}
        ></ConfirmDialog>
      </Paper>
    </div>
  );
};
export default PageDentist;
