import { makeStyles } from "@mui/styles";

const useStylesTimeSlot = makeStyles({
  container: {
    display: "flex",
  },
  button: {
    flex: "1",
    width: "100%",
    border: "1px solid green",
    fontWeight: "bold",
    color: "green",
    height: "52px",
    backgroundColor: "Transparent",
    borderRadius: "3px",
    fontSize: "1rem",
    margin: "5px",
    cursor: "pointer",
    "&:hover": {
      border: "2px solid darkgreen",
    },
    "&:focus": {
      outline: "0",
    },
  },
  confirmBtn: {
    flex: "1",
    margin: "5px",
    fontSize: "1rem",
    fontWeight: "bold",
    cursor: "pointer",
    height: "52px",
    color: "#eee",
    backgroundColor: "#00BFB1",
    borderRadius: "3px",
    border: "none",
  },
});

export default useStylesTimeSlot;
