import moment from "moment";

export const makeDentistsFunc = (dentists) => {
  return dentists.map((data) => {
    return {
      _id: data._id,
      fullName: data.name,
      phone: data.phoneNumber ? data.phoneNumber : "No Phone Number",
      color: "#00BFB1",
    };
  });
};
export const makeResourceFunc = (data) => {
  const newData = [
    {
      _id: data[0].user,
      fullName: data[0].dentist.fullName,
      phone: data[0].dentist.phone,
      color: "#00BFB1",
    },
  ];
  return newData;
};

export const makeEventFunc = (data) => {
  const newEvents = data.map((item) => {
    const newData = {
      _id: item.dentistId,
      event_id: item.dentistId,
      title: `${item.guestFirstName} - ${item.meetingName}`,
      start: new Date(moment(item.apptTime).tz(item.userTz)),
      meetingName: item.meetingName,
      end: new Date(moment(item.apptTime).tz(item.userTz).add(item.meetTime, "m")),
    };
    return newData;
  });
  return newEvents;
};
