import { makeStyles } from "@mui/styles";

const useStyleDentist = makeStyles((theme) => ({
  container: {
    height: "100vh",
    display: "flex",
    flexDirection: "column",
  },
  paper: { background: theme.palette.secondary.light, padding: "0 15rem", fontSize: "1.1rem", flex: "1 1 auto" },
  "@media (max-width: 1680px)": {
    paper: {
      padding: "0 10rem",
    },
  },
  "@media (max-width: 1280px)": {
    paper: {
      padding: "0 5rem",
    },
  },
  "@media (max-width: 880px)": {
    paper: {
      padding: "0",
    },
  },
}));

export default useStyleDentist;
