import React from "react";
import {
  Button,
  Dialog,
  Box,
  DialogContent,
  TextField,
  Select,
  MenuItem,
  Table,
  TableRow,
  TableCell,
  Checkbox,
  TableBody,
} from "@mui/material";
import moment from "moment";
import { blockSchedules } from "../../actions/appointment";
import { apiGetAllDentists } from "../../actions/dentist";

const BlockSchedulerDialog = ({
  openBlockSchedluderDialog,
  // selectedDate,
  user,
  handleChangeEvents,
  setOpenBlockSchedulerDialog,
  filteredSchedule = [],
}) => {
  const [userInfo, setUserInfo] = React.useState();
  const [dentstId, setDentistId] = React.useState("");
  const [content, setContent] = React.useState("");
  const [checked, setChecked] = React.useState([]);
  const [dentists, setDentists] = React.useState([]);
  React.useEffect(() => {
    if (user?._id) {
      setUserInfo(user._id);
    }
    apiGetAllDentists(user?._id)
      .then((res) => {
        res.status === 200 && setDentists(res.data.docs);
      })
      .catch((err) => console.log(err));
  }, [user]);

  const handleSubmit = () => {
    blockSchedules({ dentistId: dentstId, content: content, schduleIds: checked })
      .then((res) => {
        handleChangeEvents(checked);
        setOpenBlockSchedulerDialog(false);
      })
      .catch((err) => {
        console.log("Error: " + err);
      });
  };

  return (
    <Dialog fullWidth open={openBlockSchedluderDialog} maxWidth="sm" onClose={() => setOpenBlockSchedulerDialog(false)}>
      <DialogContent>
        <Box display={"flex"} alignItems="center" pb={4} justifyContent={"space-between"}>
          <Box pr={6}>Behandler</Box>
          {/* <Select fullWidth value={user._id} onChange={(e) => setUserInfo(e.target.value)}>
            <MenuItem value={user._id}>Dr.{user.fullName}</MenuItem>
          </Select> */}
          <Select fullWidth value={dentstId} onChange={(e) => setDentistId(e.target.value)}>
            {dentists?.map((dentist) => (
              <MenuItem key={`${dentist?._id}`} value={dentist?._id}>
                Dr.{dentist?.name}
              </MenuItem>
            ))}
          </Select>
        </Box>
        <Box display={"flex"} pb={4} justifyContent={"space-between"}>
          <Box pr={6}>Zeitraum</Box>
          <Box style={{ width: "100%" }}>
            <Table sx={{ border: "1px solid #ccc" }}>
              <TableBody>
                {filteredSchedule?.map((schedule, index) => {
                  return (
                    <TableRow key={index}>
                      <TableCell sx={{ border: "1px solid #ccc", padding: "8px", textAlign: "center" }}>
                        {schedule.guestFirstName}
                      </TableCell>
                      <TableCell sx={{ border: "1px solid #ccc", padding: "8px", textAlign: "center" }}>
                        {schedule.meetingName}
                      </TableCell>
                      <TableCell sx={{ border: "1px solid #ccc", padding: "8px", textAlign: "center" }}>
                        {`${moment(schedule?.apptTime).local().format("h:mm a")}`}
                      </TableCell>
                      <TableCell sx={{ border: "1px solid #ccc", padding: "8px", textAlign: "center" }}>
                        {schedule.duration}
                        {" minutes"}
                      </TableCell>
                      <TableCell sx={{ border: "1px solid #ccc", padding: "8px", textAlign: "center" }}>
                        <Checkbox
                          checked={checked.includes(schedule._id)}
                          onChange={() => {
                            if (checked.includes(schedule._id)) {
                              const currentChecked = checked.filter((check) => check !== schedule._id);

                              setChecked(currentChecked);
                            } else {
                              setChecked([...checked, schedule._id]);
                            }
                          }}
                        />
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </Box>
        </Box>
        <Box display={"flex"} pb={4} justifyContent={"space-between"}>
          <Box pr={6}>Ursache</Box>
          <TextField
            multiline
            rows={4}
            fullWidth
            value={content}
            onChange={(e) => {
              setContent(e.target.value);
            }}
          />
        </Box>
        <Box display={"flex"} justifyContent="space-evenly">
          <Button variant="outlined" onClick={(e) => setOpenBlockSchedulerDialog(false)}>
            Abbrechen
          </Button>
          <Button variant="contained" onClick={handleSubmit}>
            Abwesenheit erstellen
          </Button>
        </Box>
      </DialogContent>
    </Dialog>
  );
};
export default BlockSchedulerDialog;
