import React, { useState } from "react";
import { toast } from "react-toastify";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  Box,
  Select,
  Table,
  TableBody,
  TableRow,
  TableCell,
  MenuItem,
  Grid,
  Checkbox,
  FormControlLabel,
} from "@mui/material";
import useStylesDialog from "./stylesDialog";
import "./newEventDialog.css";
import ConfirmDialog from "../Confirm/ConfirmDialog";
import { updateSchedularEvent, getIntentList } from "../../actions/user";

const UpdateEventDialog = ({ open, handleClose, sub, dentists, editMeeting, loginedUser, id, setUser }) => {
  const classes = useStylesDialog();
  const [duration, setDuration] = useState(15);
  const preDays = {
    Monday: true,
    Tuesday: true,
    Wednesday: true,
    Thursday: true,
    Friday: true,
    Saturday: false,
    Sunday: false,
  };
  const [days, setDays] = useState(preDays);
  const [expiredTime, setExpiredTime] = useState(editMeeting.expiredTime);
  const [startDate, setStartDate] = useState(editMeeting.startDate);
  const [openDialog, setOpenDialog] = useState(false);
  const [eventTypes, setEventTypes] = useState([]);

  const [endDate, setEndDate] = useState(editMeeting.endDate);
  const [repeat, setRepeat] = useState(editMeeting.repeat);
  const [meetingName, setMeetingName] = useState(editMeeting.meetingName);
  const [allDay, setAllDay] = useState(editMeeting.allDay);
  const [dentist, setDentist] = useState(editMeeting.dentist);

  // const handleChangeDay = (selectedDay) => {
  //   console.log(selectedDay);
  //   setDays([...days, { selectedDay: !days?.selectedDay }]);
  // };
  React.useEffect(() => {
    setDays(loginedUser ? loginedUser?.availability?.days : []);
  }, [loginedUser]);
  React.useEffect(() => {
    getIntentList().then((res) => {
      setEventTypes(res.data);
    });
  }, []);
  React.useEffect(() => {
    setAllDay(editMeeting.allDay);
    setMeetingName(editMeeting.meetingName);
    setRepeat(editMeeting.repeat);
    setDays(editMeeting.days);
    setDuration(editMeeting.duration);
    setStartDate(editMeeting.startDate);
    setEndDate(editMeeting.endDate);
    setExpiredTime(editMeeting.expiredTime);
    setDentist(editMeeting.dentist);
  }, [editMeeting]);

  const handleUpdate = () => {
    const updateInfos = {
      _id: editMeeting._id,
      duration: duration,
      repeat: repeat,
      startDate: startDate,
      allDay: allDay,
      days: days,
      dentistId: dentist,
      endDate: endDate,
      meetingName: meetingName,
      expiredTime: expiredTime,
    };
    updateSchedularEvent(sub, updateInfos, editMeeting._id)
      .then((res) => {
        if (res.status === 200) {
          toast.success("Update Success!");
          setUser(res.data.doc);
          setOpenDialog(false);
          handleClose();
        }
      })
      .catch((err) => {
        toast.error("Failed Update");
      });
  };

  return (
    <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose} className="new-event-dialog">
      <DialogTitle style={{ textAlign: "center" }}>{id ? "Edit Event" : "Nenu Terminart"}</DialogTitle>
      <DialogContent>
        <Grid container spacing={4}>
          <Grid item md={6} xs={12} sm={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Absicht</Box>
              <Select
                fullWidth
                value={meetingName}
                onChange={(e) => {
                  setMeetingName(e.target.value);
                }}
              >
                {eventTypes?.map((item) => (
                  <MenuItem key={item.displayName} value={item.displayName}>
                    {item.displayName}
                  </MenuItem>
                ))}
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12} sm={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Behandler</Box>
              <Select fullWidth value={dentist} onChange={(e) => setDentist(e.target.value)}>
                {dentists.map((dentist) => (
                  <MenuItem key={`${dentist?._id}`} value={dentist?._id}>
                    Dr.{dentist?.name}
                  </MenuItem>
                ))}
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Zeitintervall</Box>
              <Select fullWidth value={duration} onChange={(e) => setDuration(e.target.value)}>
                <MenuItem key={"15"} value={15}>
                  15 min
                </MenuItem>
                <MenuItem key={"30"} value={30}>
                  30 min
                </MenuItem>
                <MenuItem key={"45"} value={45}>
                  45 min
                </MenuItem>
                <MenuItem key={"60"} value={60}>
                  60 min
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={6} xs={12}>
            <Box justifyContent="flex-end" alignItems={"center"}>
              <Box pr={2}>Stornierungsfrist</Box>
              <Select fullWidth value={expiredTime} onChange={(e) => setExpiredTime(e.target.value)}>
                <MenuItem key="time" value={24}>
                  24 studen
                </MenuItem>
              </Select>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box pr={2} mr={4}>
              Guirig von
            </Box>
            <Box>
              <Table>
                <TableBody>
                  <TableRow>
                    <TableCell
                      style={{
                        border: "solid 1px #e2e2e2",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Box pr={2} pl={2}>
                        Anfang:
                      </Box>
                      <TextField
                        style={{ width: "70%" }}
                        id="datetime-local"
                        type="datetime-local"
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true,
                        }}
                        value={startDate}
                        onChange={(e) => {
                          setStartDate(e.target.value);
                        }}
                      />
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell
                      style={{
                        border: "solid 1px #e2e2e2",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Box pr={2} pl={2}>
                        Ende:
                      </Box>
                      <TextField
                        style={{ width: "70%" }}
                        id="datetime-local"
                        type="datetime-local"
                        className={classes.textField}
                        InputLabelProps={{
                          shrink: true,
                        }}
                        value={endDate}
                        onChange={(e) => {
                          setEndDate(e.target.value);
                        }}
                      />
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell style={{ border: "solid 1px #e2e2e2" }} colSpan={2}>
                      <FormControlLabel
                        id={"-checkbox-field"}
                        classes={{ root: classes.checkBoxLabel, label: classes.checkBoxLabelLabel }}
                        control={
                          <Checkbox color="success" checked={allDay === true} onChange={(e) => setAllDay(!allDay)} />
                        }
                        label={"Ganzere Tag"}
                        style={{ paddingLeft: 8 }}
                      />
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box display={"flex"} sm={{ display: "flex" }} xs={{ display: "block" }} alignItems={"center"}>
              <Box pr={4}>Turnus</Box>
              <ul className={classes.listContainer}>
                <li className={`${repeat === 1 ? "selected" : ""}`} value={1} onClick={(e) => setRepeat(1)}>
                  Wochentage
                </li>
                <li className={`${repeat === 2 ? "selected" : ""}`} value={2} onClick={(e) => setRepeat(2)}>
                  2-Wochentage
                </li>
                <li className={`${repeat === 3 ? "selected" : ""}`} value={3} onClick={(e) => setRepeat(3)}>
                  3-Wochentage
                </li>
              </ul>
            </Box>
          </Grid>
          <Grid item md={12} xs={12}>
            <Box display={"flex"} alignItems={"center"}>
              <Box pr={4}>Wochentage</Box>
              <ul className={classes.listContainer}>
                <li
                  className={`${days && days.Monday ? "selected" : ""}`}
                  value={days?.Monday}
                  // onClick={(e) => {
                  //   handleChangeDay("Monday");
                  // }}
                >
                  Mo
                </li>
                <li
                  className={`${days && days.Tuesday ? "selected" : ""}`}
                  value={days?.Thursday}
                  // onClick={(e) => {
                  //   handleChangeDay("Thursday");
                  // }}
                >
                  Di
                </li>
                <li
                  className={`${days && days.Wednesday ? "selected" : ""}`}
                  value={days?.Wednesday}
                  // onClick={(e) => {
                  //   handleChangeDay("Wednesday");
                  // }}
                >
                  Mi
                </li>
                <li
                  className={`${days && days.Thursday ? "selected" : ""}`}
                  value={days?.Thursday}
                  // onClick={(e) => {
                  //   handleChangeDay("Thursday");
                  // }}
                >
                  Do
                </li>
                <li
                  className={`${days && days.Friday ? "selected" : ""}`}
                  value={days?.Friday}
                  // onClick={(e) => {
                  //   handleChangeDay("Friday");
                  // }}
                >
                  Fr
                </li>
                <li
                  className={`${days && days.Saturday ? "selected" : ""}`}
                  value={days?.Saturday}
                  // onClick={(e) => {
                  //   handleChangeDay("Saturday");
                  // }}
                >
                  Sa
                </li>
                <li
                  className={`${days && days.Sunday ? "selected" : ""}`}
                  value={days?.Sunday}
                  // onClick={(e) => {
                  //   handleChangeDay("Sunday");
                  // }}
                >
                  So
                </li>
              </ul>
            </Box>
          </Grid>
        </Grid>

        <Box></Box>
        <Box></Box>
      </DialogContent>
      <DialogActions>
        <Button variant="outlined" style={{ marginRight: "16px" }} onClick={handleClose}>
          Abbrechen
        </Button>

        <Button
          variant="contained"
          color="primary"
          onClick={(e) => {
            setOpenDialog(true);
          }}
        >
          Aktualisieren
        </Button>
      </DialogActions>
      <ConfirmDialog
        openDialog={openDialog}
        onOK={handleUpdate}
        onCancel={(e) => setOpenDialog(false)}
        title={"Confirm Dialog"}
        content={`Would you sure save this EventSchedule?`}
      />
    </Dialog>
  );
};

export default UpdateEventDialog;
