import { makeStyles } from "@mui/styles";

const useStylesTimeSlots = makeStyles({
  timeSlotContainer: {
    minWidth: "150px",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  btns: {
    width: "100%",
    overflowY: "scroll",
    maxWidth: "100%",
    maxHeight: "300px",
  },
  loader: {
    display: "flex",
    justifyContent: "center",
    marginTop: "1rem",
    overflowY: "hidden !important",
  },
});

export default useStylesTimeSlots;
